"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { Button } from "@/components/ui/button";

interface Preferences {
  language?: string;
  ridePreference?: string;
}

interface User {
  name: string;
  email: string;
  bio: string;
  profilePic: string;
  preferences: Preferences;
}

export default function ProfilePage() {
  const router = useRouter();

  const [user, setUser] = useState<User>({
    name: "",
    email: "",
    bio: "",
    profilePic: "",
    preferences: {
      language: "",
      ridePreference: "",
    },
  });

  useEffect(() => {
    fetch("/api/profile")
      .then((res) => res.json())
      .then((data: User) => setUser(data))
      .catch((error) => console.error("Failed to fetch profile:", error));
  }, []);

  return (
    <div className="min-h-screen w-full bg-gradient-to-b from-orange-400 to-orange-100 flex flex-col items-center py-10 px-4">
      {/* Main Profile Container */}
      <div className="w-full max-w-md bg-white rounded-lg shadow-md p-6 text-center relative">
        {/* Profile Picture */}
        <div className="flex flex-col items-center">
          <div className="w-32 h-32">
            <Image
              src={user.profilePic || "/default-avatar.png"}
              alt="Profile Picture"
              width={128}
              height={128}
              className="rounded-full object-cover border-4 border-white shadow-md"
            />
          </div>

          {/* Name & Email */}
          <h1 className="mt-4 text-2xl font-bold text-gray-800">
            {user.name || "N/A"}
          </h1>
          <p className="text-sm text-gray-500 mb-2">
            {user.email || "N/A"}
          </p>
        </div>

        {/* Bio */}
        <p className="mt-4 text-gray-700">
          {user.bio || "No bio set"}
        </p>

        {/* Preferences Section */}
        <div className="mt-6 text-left space-y-2">
          <h2 className="text-lg font-semibold text-gray-800">Preferences</h2>
          <p className="text-sm">
            <strong>Language:</strong>{" "}
            {user.preferences.language || "English"}
          </p>
          <p className="text-sm">
            <strong>Ride Preference:</strong>{" "}
            {user.preferences.ridePreference || "Comfort"}
          </p>
        </div>

        {/* Action Buttons */}
        <div className="mt-8 flex flex-col items-center space-y-3">
          <Button
            className="bg-gray-500 hover:bg-gray-600 text-white px-6 py-2"
            onClick={() => router.push("/dashboard")}
          >
            Back to Dashboard
          </Button>
          <Button
            className="bg-blue-500 hover:bg-blue-600 text-white px-6 py-2"
            onClick={() => router.push("/profileapp")}
          >
            Edit Profile
          </Button>
        </div>
      </div>
    </div>
  );
}
